import {
	request
} from '@/utils/api/request.js';

let baseAPI = "/dynamic"

// 登录
export const getFollowDynamic = (options) => {
	// return axios.post('/api/user/login', qs.stringify(data))
	return request({
		url: baseAPI + '/getFollowDynamic',
		method: 'GET',
		data: options
	})
}
